.l-btn {
    position: absolute !important;
    top: 0;
    right: 12px;
    bottom: 0;
    margin: auto;
}

.am-list {
    margin-top: 10px;
    margin-bottom: 10px;
}

.am-input-label .am-input-label-5 {
    border-right: 1px solid #e0e0e0;
}

.form-info {
    font-size: 12px;
    line-height: 12px;
    color: #999;
    text-align: center;
    margin-top: 6px;
    margin-bottom: 12px;
}

.EasyLogin_Mobile_Arrow {
    width: 6px;
    height: 6px;
    display: inline-block;
    border-right: 2px solid #bab5ad;
    border-bottom: 2px solid #bab5ad;
    transform: rotate(45deg);
    margin: 0 0 0 8px;
    position: relative;
    top: -3px;
}


.login-btn {
    line-height: 40px;
    color: #fff;
    background: #f63;
    border-radius: 4px;
    letter-spacing: 6px;
    display: block;
    text-decoration: none;
    font-size: 14px;
}


.extra .login-user {
    text-align: right;
    margin-top: 14.5px;
    display: block;
}